import "../../assets/css/env/system-env.css"
import {Util} from "../../assets/js/util";
import {useEffect, useState} from "react";

function SystemEnv() {
    const [nowDateInfo, setNowDateInfo] = useState({});
    useEffect(() => {
        getNowDateInfo();
    }, []);

    function getNowDateInfo() {
        let timeHello = Util.getTimeHello();
        let nowDate = Util.getNowDate();
        let nowDayStr = Util.getNowDay();
        let nowDateSplit = nowDate.split("-");
        setNowDateInfo({
            yearMonth: nowDateSplit[0] + "-" + nowDateSplit[1],
            date: nowDateSplit[2],
            day: nowDayStr,
            timeHello: timeHello,
        });
    }

    return (
        <div className="system-env">
            <div className="system-env-hello">
                <div className="hello-text">{nowDateInfo.timeHello}</div>
            </div>
            <div className="system-env-info">
                <div className="env-info-left">
                    {/*<div className="weather-icon flex-center"></div>*/}
                    {/*<div className="weather-text flex-center"></div>*/}
                </div>
                <div className="env-info-right">
                    <div className="year-month flex-center">{nowDateInfo.yearMonth}</div>
                    <div className="date-day">
                        <div className="date-day-date flex-center">{nowDateInfo.date}</div>
                        <div className="date-day-day">
                            {nowDateInfo.day}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default SystemEnv;