import "../../../../assets/css/xdata/record/healthChart/health-chart.css"
import {useEffect, useState} from "react";
import {Util} from "../../../../assets/js/util";
import {Empty, Popup, ProgressCircle} from "antd-mobile";
import {sortArray} from "../../../../assets/js/sort";

function HealthChart() {
    const [todoOptionList, setTodoOptionList] = useState([])
    const [todoList, setTodoList] = useState([]);
    const [doneList, setDoneList] = useState([]);
    const [donePercent, setDonePercent] = useState(0);
    const [weekDoneDays, setWeekDoneDays] = useState(0)
    const [showDoneList, setShowDoneList] = useState(false);
    const healthDateDoneDataKey = "healthDateDoneList";
    useEffect(() => {
        const totoOptionListTemp = [
            {id: 'todo1', title: '', time: '06:00'},
            {id: 'todo2', title: '', time: '10:00'},
            {id: 'todo3', title: '', time: '12:30'},
            {id: 'todo4', title: '', time: '12:30'},
            {id: 'todo5', title: '', time: '12:50'},
            {id: 'todo6', title: '', time: '14:00'},
            {id: 'todo7', title: '', time: '15:00'},
            {id: 'todo8', title: '', time: '16:00'},
            {id: 'todo9', title: '', time: '18:00'},
            {id: 'todo10', title: '30', time: '21:00'},
            {id: 'todo11', title: '', time: '22:00'},
        ];
        setTodoOptionList(totoOptionListTemp);
        let nowDoneList = getNowDoneList();
        let todoListTemp = [];
        let doneListTemp = [];
        totoOptionListTemp.forEach((item) => {
            if (nowDoneList.toString().indexOf(item.id) === -1) {
                todoListTemp.push(item);
            } else {
                doneListTemp.push(item);
            }
        });
        sortArray(todoListTemp, 'time', true);
        sortArray(doneListTemp, 'time', true);
        setTodoList(todoListTemp);
        setDoneList(doneListTemp);
        let donePercentTemp = ((doneListTemp.length / totoOptionListTemp.length * 1.0) * 100).toFixed(0);
        setDonePercent(parseInt(donePercentTemp));
        const nowDate = Util.getNowDate();
        let allDateDoneMap = getAllDateDoneMap();
        let allDoneDate = Object.keys(allDateDoneMap);
        let allDoneNumber = 0;
        allDoneDate.forEach((date) => {
            if (date !== nowDate) {
                allDoneNumber += 1;
            }
        })
        setWeekDoneDays(allDoneNumber);
    }, [todoOptionList !== []]);

    function getNowDoneList() {
        const nowDate = Util.getNowDate();
        let healthDateDoneMapJson = localStorage.getItem(healthDateDoneDataKey);
        if (!healthDateDoneMapJson) {
            healthDateDoneMapJson = "{}";
        }
        let healthDateDoneMap = JSON.parse(healthDateDoneMapJson);
        let nowDoneList = healthDateDoneMap[nowDate];
        if (!nowDoneList) {
            nowDoneList = [];
        }
        return nowDoneList;
    }

    function getAllDateDoneMap() {
        let healthDateDoneMapJson = localStorage.getItem(healthDateDoneDataKey);
        if (!healthDateDoneMapJson) {
            healthDateDoneMapJson = "{}";
        }
        let healthDateDoneMap = JSON.parse(healthDateDoneMapJson);
        return healthDateDoneMap;
    }

    function setNowDoneList(nowDoneList) {
        let healthDateDoneMapJson = localStorage.getItem(healthDateDoneDataKey);
        if (!healthDateDoneMapJson) {
            healthDateDoneMapJson = "{}";
        }
        const nowDate = Util.getNowDate();
        let healthDateDoneMap = JSON.parse(healthDateDoneMapJson);
        healthDateDoneMap[nowDate] = nowDoneList;
        localStorage.setItem(healthDateDoneDataKey, JSON.stringify(healthDateDoneMap));
    }

    function doneTodo(todo) {
        let todoListTemp = todoList;
        let doneListTemp = doneList;
        todoListTemp = todoListTemp.filter(item => item.id !== todo.id);
        doneListTemp.push(todo);
        sortArray(todoListTemp, 'time', true);
        sortArray(doneListTemp, 'time', true);
        setTodoList(todoListTemp);
        setDoneList(doneListTemp);

        let donePercentTemp = ((doneListTemp.length / todoOptionList.length * 1.0) * 100).toFixed(0);
        setDonePercent(donePercentTemp);
        let nowDoneList = getNowDoneList();
        nowDoneList.push(todo.id);
        setNowDoneList(nowDoneList);
    }

    function unTodo(todo) {
        let todoListTemp = todoList;
        let doneListTemp = doneList;
        doneListTemp = doneListTemp.filter(item => item.id !== todo.id);
        todoListTemp.push(todo);
        sortArray(todoListTemp, 'time', true);
        sortArray(doneListTemp, 'time', true);
        setDoneList(doneListTemp);
        setTodoList(todoListTemp);

        let donePercentTemp = ((doneListTemp.length / todoOptionList.length * 1.0) * 100).toFixed(0);
        setDonePercent(donePercentTemp);

        let nowDoneList = getNowDoneList();
        nowDoneList = nowDoneList.filter(item => item !== todo.id);
        debugger;
        setNowDoneList(nowDoneList);
    }

    return (
        <div className="health-card">
            <div className="health-card-left">
                <div className="left-head"></div>
                <div className="left-content flex-center">
                    <ProgressCircle style={{'--size': '110px', fontSize: 'var(--font-size-mid)'}}
                                    percent={donePercent}>{donePercent}%</ProgressCircle>
                </div>
                <div className="left-bottom flex-center">{weekDoneDays}</div>
            </div>
            <div className="health-card-right">
                <div className="right-head" onClick={() => setShowDoneList(true)}>
                    <span className="right-head-value">{doneList.length}</span>
                </div>
                <Popup
                    visible={showDoneList}
                    onMaskClick={() => {
                        setShowDoneList(false)
                    }}
                    onClose={() => {
                        setShowDoneList(false)
                    }}
                    bodyStyle={{height: '40vh'}}
                >
                    <Empty description='' className="sys-empty"
                           style={{display: doneList.length === 0 ? undefined : "none"}}/>
                    <div style={{display: doneList.length !== 0 ? undefined : "none"}}>
                        {
                            doneList.map((item, index) => {
                                return (
                                    <div className="done-list" key={index}>
                                        <div className="done-item">
                                            <div className="done-info">{item.time + " " + item.title}</div>
                                            <div className="done-btn flex-center" onClick={() => {
                                                unTodo(item)
                                            }}>
                                            </div>
                                        </div>
                                    </div>

                                );
                            })
                        }
                    </div>
                </Popup>
                <div className="right-content"
                     style={{display: todoList.length === 0 ? undefined : "none"}}>
                    <Empty description='' className="sys-empty"/>
                </div>
                <div className="right-content" style={{display: todoList.length !== 0 ? undefined : "none"}}>
                    {
                        todoList.map((todo, index) => {
                            return (
                                <div className="todo-item" key={index}>
                                    <div className="todo-item-title">{todo.title}</div>
                                    <div className="todo-item-time">{todo.time}</div>
                                    <div className="todo-item-btn flex-center" onClick={() => doneTodo(todo)}>
                                        <span></span>
                                    </div>
                                </div>
                            );
                        })
                    }
                </div>
            </div>
        </div>
    );
}

export default HealthChart;