export const ThemeUtil = {
    autoTheme() {
        //media
        let media = window.matchMedia('(prefers-color-scheme:dark)');
        if (media.matches) {
            ThemeUtil.setDark();
            // ThemeUtil.setLight();
        } else {
            ThemeUtil.setLight();
            // ThemeUtil.setDark();
        }
        //
        media.addEventListener('change', (event) => {
            if (event.matches) {
                ThemeUtil.setDark();
            } else {
                ThemeUtil.setLight();
            }
        });
    },
    setDark() {
        document.documentElement.setAttribute('theme', 'dark');
        document.querySelector('meta[name="theme-color"]').setAttribute('content', 'rgba(0, 0, 0)');
    },
    setLight() {
        document.documentElement.setAttribute('theme', 'light');
        document.querySelector('meta[name="theme-color"]').setAttribute('content', 'rgb(242, 242, 247)');
    },
}