export function sortArray(array, sortKey, asc) {
    if (!array || array.length === 0) {
        return array;
    }
    for (let i = 0; i < array.length - 1; i++) {
        for (let j = 0; j < array.length - i - 1; j++) {
            let temp;
            if (asc) {
                if (array[j + 1][sortKey] < array[j][sortKey]) {
                    temp = array[j];
                    array[j] = array[j + 1];
                    array[j + 1] = temp;
                }
            } else {
                if (array[j + 1][sortKey] > array[j][sortKey]) {
                    temp = array[j];
                    array[j] = array[j + 1];
                    array[j + 1] = temp;
                }
            }
        }
    }
    return array;
}

export function randomSort(array) {
    for (let i = array.length - 1; i > 0; i--) {
        let j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
    }
    return array;
}